/**
 * Command filtering utilities
 *
 * Filters out Claude Code and Zest plugin commands from tracking
 */

import { EXCLUDED_COMMAND_PATTERNS } from "../config/constants.js";
import { extractTextContent } from "../extractors/extraction-utils.js";
import type { ClaudeJSONLEntry } from "../types/extractors.js";
import { logger } from "./logger.js";

/**
 * Check if a message or command should be excluded from tracking
 * Filters out Claude Code and Zest plugin commands
 */
export function shouldExcludeCommand(command: string): boolean {
  const trimmedCommand = command.trim();

  // Check against all excluded patterns
  for (const pattern of EXCLUDED_COMMAND_PATTERNS) {
    if (pattern.test(trimmedCommand)) {
      return true;
    }
  }

  return false;
}

/**
 * Check if a message is a Zest command that was explicitly typed by the user
 * (not a command continuation like a single digit)
 */
export function isExplicitZestCommand(command: string): boolean {
  const trimmedCommand = command.trim();
  // Match /zest: commands (e.g., /zest:workspace, /zest:status)
  return /^\/zest[^:\s]*:/i.test(trimmedCommand);
}

/**
 * Check if a message is a digit-only command continuation (1-2 digits)
 * Used for commands like /zest:workspace where user can enter just "1" to select
 */
export function isDigitContinuation(message: string): boolean {
  const trimmed = message.trim();
  return /^\d{1,2}$/.test(trimmed);
}

/**
 * Restore filtering state by looking back at recent user messages
 *
 * For incremental extraction, we need to check if the last user message
 * was a filtered command to correctly initialize the filtering state
 *
 * @param lines - All lines from the JSONL file
 * @param lastReadLine - Last line that was already processed
 * @returns FilteringState with both flags initialized
 */
export function restoreFilteringState(lines: string[], lastReadLine: number): FilteringState {
  if (lastReadLine === 0) {
    return { filteringAssistantResponses: false, lastWasZestCommand: false };
  }

  // Look back at already-processed lines to restore filtering state
  // Check the last few lines (up to 10) to find the most recent user message
  const lookbackLines = lines.slice(Math.max(0, lastReadLine - 10), lastReadLine);

  for (let i = lookbackLines.length - 1; i >= 0; i--) {
    try {
      const entry: ClaudeJSONLEntry = JSON.parse(lookbackLines[i]);
      if (entry.message?.role === "user" && entry.message.content) {
        const textContent = extractTextContent(entry.message.content);
        if (textContent) {
          const isFiltered = shouldExcludeCommand(textContent);
          const isZestCmd = isExplicitZestCommand(textContent);

          if (isFiltered) {
            // Last user message was filtered - start with filtering enabled
            logger.debug(
              `Restored filtering state: last user message was filtered command: ${textContent.substring(0, 50)}...`,
            );
            return { filteringAssistantResponses: true, lastWasZestCommand: isZestCmd };
          }
        }
        break; // Found the last user message, stop looking
      }
    } catch {
      // Skip invalid lines
    }
  }

  return { filteringAssistantResponses: false, lastWasZestCommand: false };
}

/**
 * Filtering state for the message parser
 */
export interface FilteringState {
  filteringAssistantResponses: boolean; // Should we filter the next assistant response?
  lastWasZestCommand: boolean; // Was the last user message a zest command?
}

/**
 * Apply state-based filtering logic to a message
 *
 * State machine:
 * - When user sends excluded command: start filtering assistant responses
 * - When user sends explicit zest command: mark for potential digit continuation
 * - When user sends digit-only message after zest command: filter it and assistant response
 * - When user sends normal message: stop filtering assistant responses
 * - When assistant message arrives: filter if state flag is true
 *
 * @param role - Message role ("user" or "assistant")
 * @param textContent - Message text content
 * @param currentState - Current filtering state
 * @returns Object with shouldFilter flag and updated state
 */
export function applyMessageFilter(
  role: string,
  textContent: string,
  currentState: FilteringState,
): { shouldFilter: boolean; newState: FilteringState } {
  if (role === "user") {
    // Check if this is a digit continuation after a zest command
    if (currentState.lastWasZestCommand && isDigitContinuation(textContent)) {
      // Filter this digit message and start filtering assistant responses
      // Reset lastWasZestCommand to prevent subsequent messages from being filtered
      return {
        shouldFilter: true,
        newState: { filteringAssistantResponses: true, lastWasZestCommand: false },
      };
    }

    // Check if this is an explicit zest command first (more specific check)
    const isZestCmd = isExplicitZestCommand(textContent);
    if (isZestCmd) {
      // User sent a zest command - start filtering assistant responses and set zest flag
      return {
        shouldFilter: true,
        newState: { filteringAssistantResponses: true, lastWasZestCommand: true },
      };
    }

    // Check if user message matches other excluded command patterns
    if (shouldExcludeCommand(textContent)) {
      // User sent a non-zest excluded command - filter without setting zest flag
      return {
        shouldFilter: true,
        newState: { filteringAssistantResponses: true, lastWasZestCommand: false },
      };
    }

    // User sent normal message - stop filtering assistant responses and reset zest flag
    return {
      shouldFilter: false,
      newState: { filteringAssistantResponses: false, lastWasZestCommand: false },
    };
  }

  if (role === "assistant") {
    // If we're filtering assistant responses, skip this message
    if (currentState.filteringAssistantResponses) {
      return { shouldFilter: true, newState: currentState };
    }
  }

  // Don't filter, keep current state
  return { shouldFilter: false, newState: currentState };
}
